/**
 * Represents a message which has been prepared and is ready to send. This interface
 * is useful to get a reference to some info from the message before sending it.
 */
export interface PreparedMessage {
  /**
   * The guid of the message which will be sent
   */
  readonly messageGuid: string;

  /**
   * Sends the actual message to the receiver.
   *
   * @returns This message object
   */
  send(): PreparedMessage;
}
